.menu {
  background-color: rgb(214, 214, 214);
  width: 400px;
  margin: auto;
}

.flex {
  display: flex;
  width: 100%;
}

.flex > * {
  flex: 1 0;
}

.start {
  width: calc(100% - 10px);
  padding: 10px;
  margin: 0 5px 5px;
}
